.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: sticky;
  top: 0;
  z-index: 99;
  background: white;
  padding: 10px 0px;
}

.header_logo > a > h1 {
  font-family: 'Faster One', cursive;
  color: #f8485e;
  letter-spacing: 1.5px;
  margin-left: 20px;
}

.header_link {
  display: flex;
  justify-content: space-around;
  width: 70%;
}

.header_link > .link {
  text-decoration: none;
  font-family: "Russo One", sans-serif;
  color: #f8485e;
  border-bottom: 1px solid rgb(233, 229, 229);
  letter-spacing: 1.5px;
  font-size: 18px;
  transition: all 0.3s linear;
  position: relative;
  padding: 0 5px;
  cursor: pointer;
}

.header_link > .link::before {
  position: absolute;
  content: "";
  width: 10px;
  height: 10px;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: -1;
  transition: all 0.2s linear;
  border-radius: 10px;
}

.header_link > .link:hover {
  border: none;
  color: #fcd8d4;
}

.header_link > .link:hover::before {
  color: white;
  width: 100%;
  height: 40px;
  background-color: #f8485e;
}

.menuIcon{
  display: none !important;
  cursor: pointer;
}


@media screen and (max-width: 600px){
  .header{
    width: 100%;
    position: sticky;
    top: 0;
    border-bottom: 1px solid rgb(236, 230, 230);
  }
  .header_link{
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 55px;
    width: 100%;
    height: 300px;
    align-items: center;
    clip-path: circle(100px at 90% -40%);
    -webkit-clip-path: circle(100px at 90% -40%);
    transition: 0.4s;
    background: radial-gradient(crimson,pink);
  }
  .header_link .link{
    color:rgb(233, 229, 229);
  }
  #show_header{
    background: radial-gradient(crimson,pink);
    clip-path: circle(1000px at 90% -20%);
  -webkit-clip-path: circle(1000px at 90% -20%);
  transition: 0.4s;
  }
  .menuIcon{
    display: flex !important;
  }
}
